@import url(//netdna.bootstrapcdn.com/font-awesome/3.2.1/css/font-awesome.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Home .link .nav-link {
  padding: 0px 20px 0px 0px !important; }
  .Home .link .nav-link .icon {
    margin-right: 10px; }

.Poster .link-cart {
  color: #000; }
  .Poster .link-cart:hover {
    text-decoration: none !important; }
  .Poster .link-cart .btn-cart:hover {
    background: #138496;
    border: 1px solid #138496; }
  .Poster .link-cart .btn-cart .fas {
    margin-right: 10px; }

.ViewPoster .poster-content .poster-image .image-resize {
  width: 300px;
  height: 380px;
  object-fit: cover;
  object-position: center center; }
  .ViewPoster .poster-content .poster-image .image-resize:hover {
    -webkit-transform: scale(1.3, 1.3);
    transform: scale(1.07, 1.07); }

.ViewPoster .poster-content .poster-cart .single-item-options .category .category-title {
  font-weight: bold; }

.ViewPoster .poster-tab .tab-content .post-review fieldset.rating {
  border: none;
  float: left; }
  .ViewPoster .poster-tab .tab-content .post-review fieldset.rating > input {
    display: none; }
  .ViewPoster .poster-tab .tab-content .post-review fieldset.rating > label:before {
    margin: 5px;
    font-size: 1.25em;
    font-family: FontAwesome;
    display: inline-block;
    content: "\f005"; }
  .ViewPoster .poster-tab .tab-content .post-review fieldset.rating > .half:before {
    content: "\f089";
    position: absolute; }
  .ViewPoster .poster-tab .tab-content .post-review fieldset.rating > label {
    color: #ddd;
    float: right; }
  .ViewPoster .poster-tab .tab-content .post-review fieldset.rating > input:checked ~ label,
  .ViewPoster .poster-tab .tab-content .post-review fieldset.rating > label:hover,
  .ViewPoster .poster-tab .tab-content .post-review fieldset.rating:not(:checked) > label:hover ~ label {
    color: #138496; }
  .ViewPoster .poster-tab .tab-content .post-review fieldset.rating > input:checked + label:hover,
  .ViewPoster .poster-tab .tab-content .post-review fieldset.rating > input:checked ~ label:hover,
  .ViewPoster .poster-tab .tab-content .post-review fieldset.rating > label:hover ~ input:checked ~ label,
  .ViewPoster .poster-tab .tab-content .post-review fieldset.rating > input:checked ~ label:hover ~ label {
    color: #138496; }

.Login {
  font-family: sans-serif; }
  .Login div.box {
    position: absolute;
    top: 50%;
    left: 15%;
    -webkit-transform: translate(50%, 15%);
            transform: translate(50%, 15%);
    width: 400px;
    padding: 40px;
    background: rgba(0, 0, 0, 0.8);
    box-sizing: border-box;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
    border-radius: 10px; }
    .Login div.box h1 {
      margin: 0 0 30px;
      padding: 0;
      color: #fff;
      text-align: center; }
    .Login div.box form div.inputBox {
      position: relative; }
      .Login div.box form div.inputBox label {
        position: absolute;
        top: 0;
        left: 0;
        padding: 10px 0;
        font-size: 16px;
        color: #fff;
        letter-spacing: 1px;
        pointer-events: none;
        -webkit-transition: 0.5s;
        transition: 0.5s; }
      .Login div.box form div.inputBox input {
        width: 100%;
        padding: 10px 0px;
        font-size: 16px;
        color: #fff;
        letter-spacing: 1px;
        margin-bottom: 30px;
        border: none;
        border-bottom: 1px solid #fff;
        outline: none !important;
        background: transparent !important; }
        .Login div.box form div.inputBox input:focus ~ label,
        .Login div.box form div.inputBox input:valid ~ label {
          top: -18px;
          left: 0;
          color: #03a9f4;
          font-size: 12px; }
      .Login div.box form div.inputBox .submit {
        background: transparent;
        border: none;
        outline: none;
        color: #fff;
        background: #03a9f4;
        padding: 10px 20px;
        cursor: pointer;
        border-radius: 5px; }

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.font-bold {
  font-weight: bold !important; }

.btn-hover:hover {
  background: #138496;
  border: 1px solid #138496; }

